import React, { useState } from "react";
import Base from "../Base";
import ReactLibraryLeftPanel from "../shared-components/ReactLibraryLeftPanel";
import PageSetupLeftPanel from "../shared-components/PageSetupLeftPanel";
import ProjectElementLeftPanel from "../shared-components/ProjectElementLeftPanel";
import DatabaseLeftPanel from "../shared-components/DatabaseLeftPanel";
import { Button } from "reactstrap";
import CreateComponent from "../shared-components/CreateComponent";
import EditorComponent from "../shared-components/EditorComponent";
//import { FaEllipsisV } from "react-icons/fa";
import parse from "html-react-parser";

const Components = () => {
  const [selectedComponentMenuId, setSelectedComponentMenuId] = useState();
  const [isClose, setIsClose] = useState(false);
  const [categoryName, setCategoryName] = useState("");
  const [label1categoryName, setLabel1categoryName] = useState("");
  const [label2categoryName, setLabel2categoryName] = useState("");
  const [descriptionData, setDescriptionData] = useState("");
  const componentMenuId = (id) => {
    setSelectedComponentMenuId(id);
  };
  const description = (value, fieldname) => {
    console.log(value, fieldname);
    setDescriptionData(value);
  };
  const updateCategoryName = (value) => {
    console.log(value);
    setCategoryName(value);
  };
  const updateLabel1categoryName = (value) => {
    console.log(value);
    setLabel1categoryName(value);
  };
  const updateLabel2categoryName = (value) => {
    console.log(value);
    setLabel2categoryName(value);
  };
  const CreateComponentHandler = () => {
    console.log("categoryName", categoryName);
    console.log("label1categoryName", label1categoryName);
    console.log("label2categoryName", label2categoryName);
    console.log("descriptionData", descriptionData);
  };

  const options = {
    replace: (domNode) => {
      if (domNode.attribs && domNode.attribs.class === "remove") {
        return <></>;
      }
    },
  };

  return (
    <>
      <Base>
        <div className="component-container">
          <div
            className={`component-left py-4 px-2 t-color s-back ${
              isClose ? "isClose" : ""
            }`}
          >
            <h2>{selectedComponentMenuId}</h2>
            {/* <PageSetupLeftPanel /> */}
            <ReactLibraryLeftPanel componentMenuId={componentMenuId} />
            {/* <ProjectElementLeftPanel />
            <DatabaseLeftPanel /> */}
            {/* {ReactHtmlParser(descriptionData)} */}
            {parse(descriptionData, options)}
          </div>
          <div className="component-right px-2">
            <CreateComponent
              categoryName={categoryName}
              label1categoryName={label1categoryName}
              label2categoryName={label2categoryName}
              updateCategoryName={updateCategoryName}
              updateLabel1categoryName={updateLabel1categoryName}
              updateLabel2categoryName={updateLabel2categoryName}
            />
            <EditorComponent
              description={description}
              descriptionData={descriptionData}
              fieldname="description1"
            />
            <div className="p-3 text-end">
              label1categoryName: {label1categoryName}
              <Button
                className="primary-button"
                disabled={
                  categoryName === "" ||
                  (label1categoryName === "" && descriptionData === "")
                }
                onClick={CreateComponentHandler}
              >
                Login
              </Button>
            </div>
          </div>
          <div
            className="component-left-drawer"
            onClick={() => {
              setIsClose(!isClose);
            }}
          >
            <span>Components</span>
            <span>{/* <FaEllipsisV /> */}</span>
          </div>
        </div>
      </Base>
    </>
  );
};

export default Components;
