import React, { useState, useEffect } from "react";
import { Container, Form, FormGroup, Input, Label, Row, Col } from "reactstrap";
import ComponentMenuList from "../dummyData/ComponentMenuList";
const CreateComponent = (props) => {
  const [categoryName, setCategoryName] = useState(props.categoryName);
  const [label1categoryName, setLabel1categoryName] = useState(
    props.label1categoryName
  );
  const [label2categoryName, setLabel2categoryName] = useState(
    props.label2categoryName
  );
  const [label2categoryList, setLabel2categoryList] = useState([]);
  const componentMenuList = ComponentMenuList;
  const updateCategoryName = (e) => {
    setCategoryName(e.target.value);
    props.updateCategoryName(e.target.value);
  };
  const updateLabel1categoryName = (value) => {
    setLabel1categoryName(value);
    props.updateLabel1categoryName(value);
    const selectedLabel1 = componentMenuList.filter((cat) => {
      return cat._id === value;
    });
    selectedLabel1.length > 0
      ? setLabel2categoryList(selectedLabel1[0].label2List)
      : setLabel2categoryList([]);
    setLabel2categoryName("");
  };
  const updateLabel2categoryName = (value) => {
    setLabel2categoryName(value);
    props.updateLabel2categoryName(value);
  };
  useEffect(() => {
    let x = props.label1categoryName;
    const selectedLabel1 = componentMenuList.filter((cat) => {
      return cat._id === x;
    });
    selectedLabel1.length > 0
      ? setLabel2categoryList(selectedLabel1[0].label2List)
      : setLabel2categoryList([]);
  }, [props, componentMenuList]);
  return (
    <Container>
      {categoryName},{label1categoryName},{label2categoryName}
      <Row>
        <Col md={6} className="pt-3">
          <Form className="d-grid p-1">
            <FormGroup>
              <Label for="componentName">Component Name</Label>
              <Input
                type="text"
                id="componentName"
                placeholder="Enter Component Name"
                value={categoryName}
                onChange={updateCategoryName}
              />
            </FormGroup>
            {componentMenuList && componentMenuList.length > 0 && (
              <FormGroup>
                <Label for="label1Component">Select Label 1 Component</Label>
                <Input
                  id="label1Component"
                  name="select"
                  type="select"
                  className={label1categoryName === "" ? "text-muted" : ""}
                  value={label1categoryName}
                  onChange={(e) => updateLabel1categoryName(e.target.value)}
                >
                  <option value={""}>None</option>
                  {componentMenuList.map((label1, index) => {
                    return (
                      <option key={label1._id} value={label1._id}>
                        {label1.label1}
                      </option>
                    );
                  })}
                </Input>
              </FormGroup>
            )}
            {label1categoryName !== "" &&
              label2categoryList &&
              label2categoryList.length > 0 && (
                <FormGroup>
                  <Label for="label2Component">Select Label 2 Component</Label>
                  <Input
                    id="label2Component"
                    name="select"
                    type="select"
                    className={label2categoryName === "" ? "text-muted" : ""}
                    value={label2categoryName}
                    onChange={(e) => updateLabel2categoryName(e.target.value)}
                  >
                    <option value={""}>None</option>
                    {label2categoryList.map((label2, index) => {
                      return (
                        <option key={label2._id} value={label2._id}>
                          {label2.label2}
                        </option>
                      );
                    })}
                  </Input>
                </FormGroup>
              )}
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default CreateComponent;
