import React, { useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";

function EditorComponent(props) {
  console.log(props);
  const [value, setValue] = useState("");
  const [description, setDescription] = useState(props[0]);
  const fieldname = props[2];
  const onChangeValue = (e) => {
    // setUserInfo({
    //   ...userInfo,
    //   [e.target.name]: e.target.value,
    // });
  };
  const ondescription = (value) => {
    setDescription(value);
    props.description(value, fieldname);
  };

  //   return <ReactQuill theme="snow" value={value} onChange={setValue} />;
  return (
    <div className="col-md-12 editor px-3">
      <label className="font-weight-bold">
        Description <span className="required"> * </span>{" "}
      </label>
      <EditorToolbar toolbarId={"t1"} />
      <ReactQuill
        theme="snow"
        value={description}
        onChange={ondescription}
        placeholder={"Write something awesome..."}
        modules={modules("t1")}
        formats={formats}
      />
    </div>
  );
}

export default EditorComponent;
